import mongoose from 'mongoose';

const MONGODB_URI = process.env.MONGODB_URI;

if (!MONGODB_URI) {
  throw new Error('Please define the MONGODB_URI environment variable inside .env.local');
}

let cached = (global as { mongoose?: { conn: null | typeof mongoose; promise: null | Promise<typeof mongoose> } }).mongoose;

if (!cached) {
  cached = (global as { mongoose?: { conn: null | typeof mongoose; promise: null | Promise<typeof mongoose> } }).mongoose = { conn: null, promise: null };
}

async function connectDB() {
  // If already connected, return cached connection
  if (cached?.conn) {
    console.log('üîÑ Using cached MongoDB connection');
    return cached.conn;
  }

  // If no promise exists, create one
  if (!cached?.promise) {
    console.log('üîå Connecting to MongoDB...');
    console.log('üåç Environment:', process.env.NODE_ENV);
    console.log('üîó MongoDB URI:', MONGODB_URI?.replace(/\/\/.*@/, '//***:***@')); // Hide credentials in logs
    
    const opts = {
      bufferCommands: false,
      maxPoolSize: 10,
      serverSelectionTimeoutMS: 5000,
      socketTimeoutMS: 45000,
    };

    if (cached) {
      cached.promise = mongoose.connect(MONGODB_URI!, opts).then((mongoose) => {
        console.log('‚úÖ MongoDB connected successfully');
        console.log('üìä Database name:', mongoose.connection.name);
        return mongoose;
      });
    }
  }

  try {
    if (cached) {
      cached.conn = await cached.promise;
    }
  } catch (e) {
    console.error('‚ùå MongoDB connection failed:', e);
    if (cached) {
      cached.promise = null;
    }
    throw e;
  }

  return cached?.conn;
}

// Handle connection events
mongoose.connection.on('connected', () => {
  console.log('üü¢ Mongoose connected to MongoDB');
});

mongoose.connection.on('error', (err) => {
  console.error('üî¥ Mongoose connection error:', err);
});

mongoose.connection.on('disconnected', () => {
  console.log('üü° Mongoose disconnected from MongoDB');
});

export default connectDB;